.square {
  flex: 1 1 auto;

  border: 1px gray solid;
  display: inline-block;

  box-sizing: border-box;
  aspect-ratio: 1 / 1;
  position: relative;
}

.status {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  text-align: center;
  /* font-size: 10px; */
}
